
.swiper-pagination {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding-left: 20px;
}
.swiper-pagination .my-bullet {
  width: 25px;
  height: 4px;
  background: #FFF;
  display: block;
  opacity: .4;
  float: left;
  cursor: pointer;
  margin-right: 10px;
}
.swiper-pagination .my-bullet.active {
  opacity: 1;
}



